import TableRow from './TableRow';
import request from '../assets/request';

class Table extends React.Component { 
  constructor(props) {
    super(props)
    this.state = {
      listThesis: [],
      loading: true
    }
  }

  componentDidMount = async () => {
    localStorage.setItem('token', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjp7ImlkIjoyLCJyb2xlIjoiTEVDIn0sImlhdCI6MTU1Nzg5MTAxMSwiZXhwIjoxNTU3OTc3NDExfQ.t7B8GrVc4dpwmWanM4pkFsznhUh_FRmDPpLW0GxvUQE');
    localStorage.setItem('userRole', 'LEC');
    localStorage.setItem('userId', 2);
    const res = await request('/thesis/info')
    this.setState({listThesis: res.result, loading: false})
  }

  render = () => {
    const {onChange, filter, search} = this.props;
    return (
    <div className="table-container">
          <table className="table table-filter">
            <thead className="theadTitle">
              <tr>
                <td colSpan={3}>Khóa luận</td>
                <td>Trạng thái</td>
                <td>Ngày cập nhật</td>
                <td colSpan={2}>Hành động</td>
              </tr>
            </thead>
            <tbody>
              {!this.state.loading? this.renderListThesis(search, filter) : this.renderLoading()}
            </tbody>
          </table>
    </div>
  )}

  renderListThesis = (search, filter) => {
    let check = false;
    let result = this.state.listThesis.map((ele, index) => {
        if( (ele.state === filter || (filter === 'ALL')) && ((ele.thesisSubject + `(${ele.thesisCode})`).includes(search)) )
          return <TableRow {...ele} key={index}/>
      })
    for( let i = 0; i < result.length; i++) if(result[i]) check = true;
    if(!check) return (
      <tr>
        <td colSpan="7" style={{verticalAlign: "center", textAlign: "center", fontSize: "3rem", color: "#ddd"}} className="fadein_ele">
          <i className="fas fa-database"></i> NO DATA
        </td>
      </tr>
    )
    else return result
  }

  renderLoading = () => {
    return (
      <tr>
        <td colSpan="7" style={{verticalAlign: "center", textAlign: "center"}} className="fadein_ele">
          <i className="fa fa-spinner fa-spin"></i> Loading...
        </td>
      </tr>
    )
  }
}

export default Table